import React, { Component } from 'react';
import avatar from '../style/imgs/b1.jpg';

import { Menu, Icon, Layout } from 'antd';
import { withRouter } from 'react-router-dom';
import { connectAlita } from 'redux-alita';
import { removeAccount } from '../utils/account';
import { removeToken } from '../utils/token';


import styles from '../style/HeaderCustom.module.css';

const { Header } = Layout;
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class HeaderCustom extends Component {
  state = {
    user: '',
    visible: false,
  };
  componentDidMount() {
  };

  menuClick = e => {
    console.log(e);
    e.key === 'logout' && this.logout();
  };

  logout = () => {
    removeAccount();
    removeToken();
    this.props.history.push('/login')
  };

  popoverHide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <Header>
        {
          <Icon
            type={this.props.collapsed ? 'menu-unfold' : 'menu-fold'}
            onClick={this.props.toggle}
          />
        }
        <Menu className={styles.menu}
          mode="horizontal"
          onClick={this.menuClick}>
          <SubMenu title={
            <span className={styles.avatar}>
              <img src={avatar} alt="头像" />
              <i className={`${styles.bottom} ${styles.on}`} />
            </span>
          }>
            <MenuItemGroup title="user center">
              <Menu.Item key="setting:1">Hello - {this.props.user}</Menu.Item>
              <Menu.Item key="logout"><span onClick={this.logout}>logout</span></Menu.Item>
            </MenuItemGroup>
          </SubMenu>
        </Menu>
      </Header>
    )
  }
}

export default withRouter(connectAlita(['responsive'])(HeaderCustom));
