import React from 'react';
import { Input, Form, Button, message } from 'antd';
import { unitWordCount } from '../../axios/index';

class MaterialForm extends React.Component {

  componentWillMount() {
    const { unitId } = this.props;
    console.log(unitId);
    if (!unitId) return;

    unitWordCount(+unitId)
      .then(data => {
        if (!data) {
          message.warn('get word count failed');
        } else {
          const form = this.props.form;
          form.setFieldsValue({ wordCount: JSON.stringify(data) });
        }
      })
  }

  handleSubmit = (event) => {
    event.preventDefaut();
    const form = this.props.form;
    form.validateFields((err, values) => {
      if (err) {
        message.warn(err.message);
      } else {
        const { onSave } = this.props;
        onSave(values);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit}>
        <Form.Item>
          {
            getFieldDecorator('wordCount',
              { rules: [{ required: true, message: 'please input word count' }], })(
                <Input.TextArea rows={20} />
              )
          }
        </Form.Item>
        <Button type="primary" htmlType="submit">Save</Button>
      </Form>

    );
  }
}

export default Form.create()(MaterialForm);