import React from 'react';
import { Row, Col, Button } from 'antd';
import BreadcrumbCustom from '../BreadcrumbCustom';
import styles from '../../style/UnitHeader.module.css';

class UnitHeader extends React.Component {
  render() {
    const { location, showAddButton, onAddClicked } = this.props;

    const actionEle = showAddButton && <Col span={12} push={8}>
      <Button type="primary" onClick={onAddClicked}>新增</Button>
    </Col>

    return (
      <Row className={styles.header} >
        <Col span={12} className={styles.breadcrumb}>
          <BreadcrumbCustom location={location} />
        </Col>
        {actionEle}
      </Row>
    );
  }
}

export default UnitHeader;


