import React from 'react';
import { Table, Button, Modal, Form, Input, Select, message } from 'antd';


const MOCK_USER_LIST = [
  {
    userID: 1,
    userName: 'TEST1',
    school: 'SCHOOL1',
    role: 'ROLE1',
    action: ['Edit', 'Delete']
  },
  {
    userID: 2,
    userName: 'TEST2',
    school: 'SCHOOL2',
    role: 'ROLE2',
    action: ['Edit', 'Delete']
  }
];


class EditModal extends React.Component {
  handleOK = () => {
    const form = this.props.form;
    form.validateFields((err, values) => {
      if (err) {
        message.warn('invalid input');
      } else {
        const { onSave } = this.props;
        onSave(values);
      }
    });
  }

  handleCancel = () => {
    const { onCancel } = this.props;
    onCancel();
  }

  render() {
    const { visible, content } = this.props;
    const { getFieldDecorator } = this.props.form;

    const { userID, userName, school, role } = content || {};

    return (
      <Modal
        title="Edit user"
        destroyOnClose={true}
        visible={visible}
        onOk={this.handleOK}
        onCancel={this.handleCancel}>
        <Form>
          <Form.Item>
            {
              getFieldDecorator('userID', { initialValue: userID })(
                <Input style={{ display: 'None' }} />
              )
            }
          </Form.Item>
          <Form.Item label="UserName">
            {
              getFieldDecorator('userName',
                {
                  rules: [{ required: true, message: 'please input UserName!' }],
                  initialValue: userName
                })(
                  <Input />
                )
            }
          </Form.Item>
          <Form.Item label="School">
            {
              getFieldDecorator('school',
                {
                  rules: [{ required: true, message: 'please input School!' }],
                  initialValue: school
                })(
                  <Input />
                )
            }
          </Form.Item>
          <Form.Item label="Role">
            {
              getFieldDecorator('role',
                {
                  rules: [{ required: true, message: 'please select Role!' }],
                  initialValue: role
                })(
                  <Select>
                    <Select.Option value="ROLE1">ROLE1</Select.Option>
                    <Select.Option value="ROLE2">ROLE2</Select.Option>
                  </Select>
                )
            }
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

EditModal = Form.create()(EditModal);

const DEFAULT_PAGE_SIZE = 10;
export default class Permisson extends React.Component {

  columns = [
    {
      title: 'UserID',
      dataIndex: 'userID',
      key: 'userID'
    },
    {
      title: 'UserName',
      dataIndex: 'userName',
      key: 'userName'
    },
    {
      title: 'School',
      dataIndex: 'school',
      key: 'school'
    },
    {
      title: 'Role',
      dataIndex: 'role',
      key: 'role'
    },
    {
      title: 'Action',//TODO: show which action by user permisson
      dataIndex: 'action',
      key: 'action',
      render: (text, record) => {
        return text && text.map(item => {
          if (item === 'Edit') {
            return <Button
              key={item}
              onClick={(event) => this.handleEditUserButtonClicked(event, record)}>
              Edit
              </Button>
          } else if (item === 'Delete') {
            return <Button
              key={item}
              onClick={(event) => this.handleDeleteUserButtonClicked(event, record)}>
              Delete
              </Button>
          } else {
            return null;
          }
        });
      }
    },
  ]


  state = {
    userList: [],
    count: 0,
    curEditUser: null,
    showEditModal: false
  }

  componentWillMount() {
    this.setState({ userList: MOCK_USER_LIST, count: MOCK_USER_LIST.length });
  }

  // table edit action callback
  handleEditUserButtonClicked = (event, record) => {
    event.preventDefault();
    this.setState({
      curEditUser: record,
      showEditModal: true
    });
  }

  // table delete action callback
  handleDeleteUserButtonClicked = (event, record) => {
    event.preventDefault();
    const { userList } = this.state;
    const updatedUserList = userList.filter(user => {
      return user.userID !== record.userID;
    });
    this.setState({ userList: updatedUserList });
  }

  // edit modal callback
  handleEditModalSave = (values) => {
    const { userList } = this.state;
    const updatedUserList = userList.slice();

    let isAddNewUserModel = true;

    for (let i = 0; i < updatedUserList.length; ++i) {
      if (updatedUserList[i].userID === values.userID) {
        let user = updatedUserList[i];
        updatedUserList[i] = { ...user, ...values };
        isAddNewUserModel = false;
        break;
      }
    }

    if (isAddNewUserModel) {
      let userID = updatedUserList.length;
      // mock data
      updatedUserList.push({ ...values, userID: ++userID, action: ['Edit', 'Delete'] });
    }

    this.setState({
      showEditModal: false,
      userList: updatedUserList
    });
  }

  // edit modal callback
  handleEditModalCancel = () => {
    this.setState({ showEditModal: false });
  }

  handleAddNewUser = (event) => {
    event.preventDefault();
    this.setState({
      curEditUser: null,
      showEditModal: true
    });
  }

  render() {
    const { count, userList, showEditModal, curEditUser } = this.state;

    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }

    return (
      <div>
        <Table
          columns={this.columns}
          rowKey={record => record.userID}
          dataSource={userList}
          pagination={pageOptions}
        />

        <Button
          onClick={this.handleAddNewUser}>
          Add user
        </Button>

        <EditModal
          visible={showEditModal}
          onSave={this.handleEditModalSave}
          onCancel={this.handleEditModalCancel}
          content={curEditUser} />
      </div>

    );
  }
}