import React from 'react';
import { Breadcrumb } from 'antd';
import routes from './config';

export function GetBreadcrumbItemFromRoutePath(path) {
  const menuRoutes = routes.menus;

  const result = GetBreadcrumbItemFromRoutePathImpl(menuRoutes, path);
  return result;
}

function GetBreadcrumbItemFromRoutePathImpl(routes, path) {
  for (let i = 0; i < routes.length; ++i) {
    const route = routes[i];
    if (route.key === path) {
      return (
        <Breadcrumb.Item key={route.key}>
          {route.title}
        </Breadcrumb.Item>
      );
    } else {
      if (route.subs) {
        const result = GetBreadcrumbItemFromRoutePathImpl(route.subs, path);
        if (result) {
          return result;
        }
      }
    }
  }

  return null;
}