import React from 'react';
import { Row, Col, Form, Upload, message, Button, Icon, Modal, Input, Checkbox } from 'antd';
import { UPLOAD_PPT } from '../../axios/config';
import { extractAssertByHashId, lessonWordCount, updateAssetLessonID } from '../../axios/index';


class WordCountEdit extends React.Component {
  render() {
    const { visible, onCancelWordCount, onSaveWordCount, content } = this.props;
    const { getFieldDecorator } = this.props.form;
    return (
      <Modal
        title="Edit word count result"
        visible={visible}
        onCancel={onCancelWordCount}
        onOk={onSaveWordCount}
      >
        <Form>
          <Form.Item>
            {
              getFieldDecorator('content', {
                initialValue: content
              })(
                <Input.TextArea rows={10} />
              )
            }
          </Form.Item>
        </Form>
      </Modal >
    );
  }
}

const WordCountEditModal = Form.create()(WordCountEdit);


class LessonMaterial extends React.Component {
  state = {
    pptList: [],
    pptHashId: '',
    pptExtractWordCount: true,
    pptWordCountContent: '',
    tableResList: [],
    digitalResList: [],
    stuReadResList: [],
    showWordCountEdit: false
  }

  onChange = (info) => {
    const { file, fileList } = info;

    if (file.status !== 'uploading') {
      console.log('uploading file');
    }
    if (file.status === 'done') {
      const { response } = file;
      if (response.isSuccess) {
        message.success(`${file.name} file uploaded successfully`);
        const { hashid, id, name, url } = response.data;
        const { lessonID } = this.props;
        //update asset lessonID
        updateAssetLessonID(id, name, hashid, url, +lessonID)
          .then(data => {
            if (!data) {
              message.warn('update asset lessonID failed');
            } else {
              const { pptExtractWordCount } = this.state;
              if (pptExtractWordCount) {
                // call extract assert api if needed
                extractAssertByHashId(hashid);
              }
              this.setState({ pptHashId: hashid });
            }
          });
      } else {
        message.error(`${file.name} file upload failed.`);
      }
    } else if (file.status === 'error') {
      message.error(`${file.name} file upload failed.`);
    }

    this.setState({ pptList: [...fileList] });
  }

  handleEditWordCountResultClicked = () => {
    const { lessonID } = this.props;
    if (lessonID) {
      lessonWordCount(+lessonID)
        .then(data => {
          this.setState({
            pptWordCountContent: JSON.stringify(data),
            showWordCountEdit: true
          })
        });
    }
  }

  handleSaveWordCountClicked = () => {
    const editWordCountForm = this.editWordCountForm;

    editWordCountForm.validateFields((err, values) => {
      if (err) {
        message.warn('save word count failed');
      } else {
        console.log(values);
      }
    });

    this.setState({ showWordCountEdit: false });
  }

  handleCancelWordCountClicked = () => {
    this.setState({ showWordCountEdit: false });
  }

  saveWordCountEditFormRef = (formRef) => {
    this.editWordCountForm = formRef;
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { pptList,
      pptHashId,
      pptExtractWordCount,
      pptWordCountContent,
      tableResList,
      digitalResList,
      stuReadResList,
      showWordCountEdit } = this.state;

    const uploadProps = {
      onChange: this.onChange
    };

    const EditPPT = pptHashId ?
      <Button type="primary" onClick={this.handleEditWordCountResultClicked}>WordCountResult</Button> : null;

    return (
      <Form>
        <Row>
          <h3>LessonPPT:</h3>
          <Col span={10}>
            <Form.Item label="">
              {
                getFieldDecorator('pptList', {
                  rules: [{ required: true, message: 'please Select Lesson PPT!' }]
                })(
                  <Upload
                    name="file"
                    accept=".ppt,.pptx"
                    showUploadList={false}
                    action={UPLOAD_PPT}
                    onChange={this.onChange}
                    fileList={pptList}
                  >
                    <Button>
                      <Icon type="upload" /> Click to Upload
                    </Button>
                  </Upload>
                )
              }
            </Form.Item>
          </Col>
          <Col span={14}>
            <Form.Item >
              {
                getFieldDecorator('cbExtractWordCount')(
                  <Checkbox checked={pptExtractWordCount}>Extract word count</Checkbox>
                )
              }
            </Form.Item>
          </Col>
        </Row>

        {EditPPT}

        <Form.Item label="Table Resource">
          {
            getFieldDecorator('tableResList', {})(
              <Upload
                fileList={tableResList}
                {...uploadProps} >
                <Button>
                  <Icon type="upload" /> Click to Upload
                </Button>
              </Upload>
            )
          }
        </Form.Item>
        <Form.Item label="Digital Resource">
          {
            getFieldDecorator('digitalResList', {})(
              <Upload
                fileList={digitalResList}
                {...uploadProps} >
                <Button>
                  <Icon type="upload" /> Click to Upload
                </Button>
              </Upload>
            )
          }
        </Form.Item>
        <Form.Item label="Student Reading Resource">
          {
            getFieldDecorator('stuReadResList', {})(
              <Upload
                fileList={stuReadResList}
                {...uploadProps} >
                <Button>
                  <Icon type="upload" /> Click to Upload
                </Button>
              </Upload>
            )
          }
        </Form.Item>

        <WordCountEditModal
          ref={this.saveWordCountEditFormRef}
          visible={showWordCountEdit}
          content={pptWordCountContent}
          onSaveWordCount={this.handleSaveWordCountClicked}
          onCancelWordCount={this.handleCancelWordCountClicked}
        />
      </Form>
    );
  }
}

export default Form.create()(LessonMaterial);