import React from 'react';
import { Table } from 'antd';
import { withRouter } from 'react-router-dom';
import CampaignHeader from './CampaignHeader';
import { getProgramList } from '../../axios/index';

const columns = [
  {
    title: 'ProgramID',
    dataIndex: 'programID',
    key: 'programID',
    render: (text, record) => <a href={`#/app/campaign/edit/?id=${record.id}`}>{text}</a>
  },
  {
    title: 'Program Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Grade',
    dataIndex: 'grade',
    key: 'grade',
  },
  {
    title: 'Semester',
    dataIndex: 'semester',
    key: 'semester'
  },
  {
    title: 'Description',
    dataIndex: 'summary',
    key: 'summary'
  }
];

const DEFAULT_PAGE_SIZE = 10;

class CampaignHome extends React.Component {

  state = {
    programList: [],
    count: 0
  }

  handleAddProgram = (event) => {
    event.preventDefault();
    this.props.history.push('/app/campaign/add');
  }

  componentWillMount() {
    getProgramList(0, 10).then(data => {
      if (data) {
        const { programList, count } = data;
        this.setState({ programList, count });
      }
    });
  }

  handlePageChange = (page, pageSize) => {
    const offset = (+page - 1) * pageSize;
    getProgramList(offset, pageSize).then(data => {
      const { programList, count } = data;
      this.setState({ programList, count });
    });
  }

  render() {
    const { location } = this.props;
    const { programList, count } = this.state;
    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }
    return (
      <div>
        <CampaignHeader
          location={location}
          onAddClicked={this.handleAddProgram}
          showAddButton={true} />

        {programList &&
          <Table
            columns={columns}
            rowKey={record => record.id}
            dataSource={programList}
            pagination={pageOptions} />
        }

      </div>
    );
  }
}

export default withRouter(CampaignHome);