import React from 'react';
import { withRouter } from 'react-router-dom';
import { Tabs } from 'antd';
import queryString from 'query-string';
import CampaignHeader from './CampaignHeader';
import CampaignBaseInfoForm from '../forms/CampaignBaseInfoForm';
import CampaignUnit from './CampaignUnit';
import Permisson from '../Pages/Permisson';


import styles from '../../style/EditCampaign.module.css';

const { TabPane } = Tabs;

class EditCampaign extends React.Component {
  state = {
    activeKey: '1',
    id: ''// programId in database
  }

  componentWillMount() {
    const parsed = queryString.parse(this.props.location.search);
    const { id } = parsed;
    this.setState({ id });
  }


  handleTabChange = (activeKey) => {
    this.setState({ activeKey });
  }

  handleAddBaseInfo = (id) => {
    this.setState({ activeKey: '2', id });
  }

  render() {
    const { location } = this.props;
    const { activeKey, id } = this.state;

    return (
      <div>
        <CampaignHeader location={location} />
        <Tabs
          className={styles.content}
          activeKey={activeKey}
          onChange={this.handleTabChange}
          type="card"
          tabBarGutter={4}>
          <TabPane tab="基础信息" key="1" className={styles.tabPane}>
            <CampaignBaseInfoForm
              id={id}
              isEditType={true}
              onAddBaseInfo={this.handleAddBaseInfo}
            />
          </TabPane>
          <TabPane tab="单元结构" key="2" className={styles.tabPane}>
            {activeKey === '2' ? <CampaignUnit id={id} /> : null}
          </TabPane>
          <TabPane tab="角色设置" key="3" className={styles.tabPane}>
            {activeKey === '3' ? <Permisson /> : null}
          </TabPane>
          <TabPane tab="资料管理" key="4" className={styles.tabPane}>
            Content of Tab Pane 4
          </TabPane>
        </Tabs>,
    </div>);
  }
}


export default withRouter(EditCampaign);