
import { post } from './tools';
import { getToken } from '../utils/token';
import * as config from './config';

export const login = (userName, password) =>
  post({
    url: config.LOGIN,
    data: {
      userName,
      password
    },
    msg: 'login failed'
  });

export const getProgramList = (offset, count) => {
  const sessionToken = getToken() || '';
  return post({
    url: config.GET_PROGRAM_LIST,
    data: {
      sessionToken,
      offset,
      count
    }
  });
}

