export const assertUpload = (data) => {
  const sessionToken = getToken() || '';
  const uploadData = data;
  uploadData.sessionToken = sessionToken;
  return post({
    url: config.UPLOAD_PPT,
    data: uploadData,
    config: {
      headers: {
        'content-type': 'multipart/form-data',
      }
    }
  });
}

