import React from 'react';
import { Form, Input, Row, Col, Select, Button } from 'antd';
import { getLessonInfoById, createLesson, updateLessonById } from '../../axios/index';
import styles from '../../style/LessonBaseInfoForm.module.css'


const { TextArea } = Input;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};


/**
 * 如果是编辑模式，填充后台数据
 * 如果是新增模式，只拿一些下拉列表的数据
 */
class LessonBaseInfoForm extends React.Component {

  state = {

  }

  componentWillMount() {
    const { isEditType, lessonId } = this.props;
    if (isEditType && lessonId) {
      // get lesson info from server
      getLessonInfoById(+lessonId).then(data => {
        if (!data) return;

        const form = this.props.form;
        const { objectives } = data;
        const threeObj = objectives ? JSON.parse(objectives) : {};

        const initData = {
          lessonNo: data.lessonNo,
          userID: data.userID,
          description: data.description,
          title: data.title,
          focus: data.focus,
          stage: data.stage,
          timelength: data.timelength,
          imperatives: data.imperatives,
          unitID: data.unitID,
          ...threeObj
        };
        form.setFieldsValue(initData);
      });
    }
  }

  handleSubmit = (event) => {
    event.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // send to backend
        const { understanding, languageCapablity, socioSkill } = values;
        const threeObj = {
          understanding,
          languageCapablity,
          socioSkill
        };
        const {
          unitID,
          userID,
          lessonNo,
          title,
          timelength,
          focus,
          imperatives,
          description,
          stage
        } = values;

        const requestBody = {
          unitID,
          userID,
          lessonNo,
          title,
          timelength,
          focus,
          imperatives,
          description,
          stage,
          objectives: JSON.stringify(threeObj)
        };

        const { isEditType, lessonId, onAddBaseInfo } = this.props;
        if (isEditType && lessonId) {
          updateLessonById(+lessonId, requestBody).then(data => {
            if (data) {
              const { id } = data;
              onAddBaseInfo(id);
            }

          });
        } else {
          createLesson(requestBody).then(data => {
            if (data) {
              const { id } = data;
              onAddBaseInfo(id);
            }
          })
        }
      }
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    const { programCode } = this.props;

    return (
      <Form onSubmit={this.handleSubmit}>
        <Row>
          <Col span={12}>
            <Form.Item label='LessonCode' {...formItemLayout}>
              <span>{programCode}</span>
              {getFieldDecorator('lessonNo', {
                rules: [{ required: true, message: 'please input Lesson Code' }]
              })(
                <Input className={styles.lessonCode} />
              )}
            </Form.Item>
          </Col>

          <Col span={12}>
            <Form.Item label='Ower' {...formItemLayout}>
              {getFieldDecorator('userID', {
                rules: [{ required: true, message: 'please input Lesson ower' }]
              })(
                <Input />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Form.Item label='Title' {...formItemLayout}>
          {getFieldDecorator('title', {
            rules: [{ required: true, message: 'please input Lesson Title' }]
          })(
            <Input />
          )}
        </Form.Item>

        <Form.Item label='Focus' {...formItemLayout}>
          {getFieldDecorator('focus', {
            rules: [{ required: true, message: 'please input Lesson Focus' }]
          })(
            <Input />
          )}
        </Form.Item>

        <Row>
          <h3 style={{ marginLeft: 10 }}>Descritpion: </h3>
          <Col>
            <Form.Item >
              {getFieldDecorator('description', {
                rules: [{ required: true, message: 'please input description' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Form.Item label="Entrepreneurship Stage" {...formItemLayout}>
          {
            getFieldDecorator('stage', {
              rules: [{ required: true, message: 'please select Entrepreneurship Stage' }],
            })(
              <Select>
                <Select.Option value="stage1">Entrepreneurship Stage1</Select.Option>
                <Select.Option value="stage2">Entrepreneurship Stage2</Select.Option>
                <Select.Option value="stage3">Entrepreneurship Stage3</Select.Option>
                <Select.Option value="stage4">Entrepreneurship Stage4</Select.Option>
                <Select.Option value="stage5">Entrepreneurship Stage5</Select.Option>
                <Select.Option value="stage6">Entrepreneurship Stage6</Select.Option>
                <Select.Option value="stage7">Entrepreneurship Stage7</Select.Option>
                <Select.Option value="stage8">Entrepreneurship Stage8</Select.Option>
                <Select.Option value="stage9">Entrepreneurship Stage9</Select.Option>
              </Select>
            )
          }
        </Form.Item>

        <Form.Item label="Duration" {...formItemLayout}>
          {
            getFieldDecorator('timelength', {
              rules: [{ required: true, message: 'please input Duration' }],
            })(
              <Input />
            )
          }
        </Form.Item>

        <Form.Item label="Imperatives" {...formItemLayout}>
          {
            getFieldDecorator('imperatives', {
              rules: [{ required: true, message: 'please input imperatives' }],
            })(
              <Input />
            )
          }
        </Form.Item>

        <Form.Item label="所属单元" {...formItemLayout}>
          {
            getFieldDecorator('unitID', {
              rules: [{ required: true, message: 'please input unit name' }],
            })(
              <Select>
                <Select.Option value="UnitName1">UnitName1</Select.Option>
                <Select.Option value="UnitName2">UnitName2</Select.Option>
                <Select.Option value="UnitName3">UnitName3</Select.Option>
              </Select>
            )
          }
        </Form.Item>

        <Row>
          <h3 style={{ marginLeft: 10 }}>Three Fold Learning Objective</h3>
          <Col>
            <Form.Item label="Building the entrepreneurial Understanding">
              {getFieldDecorator('understanding', {
                rules: [{ required: true, message: 'please select Program Age Level' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop Language Capablity">
              {getFieldDecorator('languageCapablity', {
                rules: [{ required: true, message: 'please input Develop Language Capablity' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop socio-emotional skills">
              {getFieldDecorator('socioSkill', {
                rules: [{ required: true, message: 'please input socio-emotional skills' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

        </Row>

        <Row className={styles.action}>
          <Col span={2} offset={10}>
            <Button type="primary" htmlType="submit">Save</Button>
          </Col>
          <Col span={2}>
            <Button >Cancel</Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create()(LessonBaseInfoForm);