import React from 'react';
import { Modal, Form, Input } from 'antd';

const AddProcessFormModal = Form.create()(
  (props) => {
    const { visible, confirmLoading, onCancel, onOk } = props;
    const { getFieldDecorator } = props.form;
    return (
      <Modal
        visible={visible}
        confirmLoading={confirmLoading}
        onCancel={onCancel}
        onOk={onOk}>
        <Form >
          <Form.Item label="Process Name">
            {
              getFieldDecorator('processName', {
                rules: [{ required: true, message: 'please input Process Name!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="Process Objective">
            {
              getFieldDecorator('processObjective', {
                rules: [{ required: true, message: 'please input Process Objective!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
        </Form>
      </Modal>
    );
  }
);
export default AddProcessFormModal;