import React from 'react';
import { Table, Button, message } from 'antd';
import { getTaskByLessonId, getStepByTaskId, createTask, createStep } from '../../axios/index';
import AddTaskInfoForm from '../forms/AddTaskInfoForm';

const columns = [
  {
    title: 'Task ID',
    dataIndex: 'taskID',
    key: 'taskID',
    render: (text) => text
  },
  {
    title: 'Task Title',
    dataIndex: 'taskTitle',
    key: 'taskTitle',
  },
  {
    title: 'Step Count',
    dataIndex: 'stepCount',
    key: 'stepCount',
    render: (text, record) => { return (record.steps && record.steps.count) || 0; }
  }
];

const DEFAULT_PAGE_SIZE = 10;

export default class Task extends React.Component {

  state = {
    taskList: [],
    count: 0,
    showAddTaskInfo: false
  }

  componentWillMount() {
    //GET task info by id
    const { lessonID } = this.props;
    getTaskByLessonId(+lessonID, 0, DEFAULT_PAGE_SIZE)
      .then(data => {
        if (!data) return;
        const { taskList, count } = data;

        const promiseList = [];

        // get step by task id and return {id, responseData}
        taskList.forEach(item => {
          promiseList.push(getStepByTaskId(+item.id).then(data => { return { id: item.id, data } }));
        });

        Promise.all(promiseList).then(res => {
          // add steps data for each task
          res.forEach(item => {
            for (let i = 0; i < taskList.length; ++i) {
              if (taskList[i] && taskList[i].id === item.id) {
                taskList[i].steps = item.data;
                break;
              }
            }
          });

          this.setState({ taskList, count });
        });
      });
  }

  handleAddTaskClicked = () => {
    this.setState({ showAddTaskInfo: true });
  };

  handleSaveTaskClicked = (taskInfo) => {
    const { lessonID } = this.props;
    if (!lessonID) {
      message.warn('please save lesson info firstly');
      return;
    }

    const { taskID, taskTitle, steps } = taskInfo;

    createTask(+lessonID, taskID, taskTitle)
      .then(res => {
        // task id in database
        const { id } = res;

        const addStepsPromiseList = [];

        steps.forEach(stepInfo => {
          addStepsPromiseList.push(createStep(+id, stepInfo));
        });

        Promise.all(addStepsPromiseList).then((addStepsRes) => {
          const { taskList } = this.state;
          const steps = {
            stepList: addStepsRes,
            count: addStepsRes.length
          }

          res.steps = steps;
          const updatedTaskList = taskList.concat(res);
          console.log(updatedTaskList);
          this.setState({
            taskList: updatedTaskList,
            showAddTaskInfo: false
          });
        });
      });
  }

  handleCancelSaveTaskClicked = () => {
    this.setState({ showAddTaskInfo: false });
  }


  handlePageChange = (page, pageSize) => {
    const { lessonID } = this.props;
    const offset = (+page - 1) * pageSize;
    getTaskByLessonId(+lessonID, offset, DEFAULT_PAGE_SIZE)
      .then(data => {
        if (!data) return;
        const { taskList, count } = data;
        this.setState({ taskList, count });
      });
  }


  render() {
    const { lessonID } = this.props;
    const { taskList, count, showAddTaskInfo } = this.state;


    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }
    return (
      <div>
        <Button
          onClick={this.handleAddTaskClicked}
          type="primary"
          style={{ marginBottom: 16 }}>
          Add Task
        </Button>


        {taskList &&
          <Table
            columns={columns}
            rowKey={record => record.id}
            dataSource={taskList}
            pagination={pageOptions} />
        }

        <AddTaskInfoForm
          id={lessonID}
          visible={showAddTaskInfo}
          onCancel={this.handleCancelSaveTaskClicked}
          onSaveTask={this.handleSaveTaskClicked} />

      </div>
    );
  }
}