import React from 'react';
import { Modal, Form, Icon, Input, message, Row, Col, Select, Table } from 'antd';
import styles from '../../style/AddTaskInfoForm.module.css';


class AddTaskStepFormModal extends React.Component {

  handleSaveTaskStepClicked = () => {
    const form = this.props.form;
    form.validateFields((err, values) => {
      if (err) {
        message.warn('save task step failed!');
      } else {
        const { onSaveStep } = this.props;
        onSaveStep(values);
      }
    });
  }

  handleCancelTaskStepClicked = () => {
    const { onCancel } = this.props;
    onCancel();
  }

  render() {
    const { visible, confirmLoading } = this.props;
    const { getFieldDecorator } = this.props.form;

    const formItemLayout = {
      labelCol: {
        span: 10
      },
      wrapperCol: {
        span: 12
      },
    }

    return (
      <Modal
        title="Add Step"
        visible={visible}
        confirmLoading={confirmLoading}
        onCancel={this.handleCancelTaskStepClicked}
        onOk={this.handleSaveTaskStepClicked}
      >
        <Form>
          <Row>
            <Col span={10}>
              <Form.Item label="Step ID" {...formItemLayout}>
                {
                  getFieldDecorator('stepID', {
                    rules: [{ required: true, message: 'please input Step ID!' }]
                  })(
                    <Input />
                  )
                }
              </Form.Item>
            </Col>

            <Col span={14}>
              <Form.Item label="Estimate time"  {...formItemLayout}>
                {
                  getFieldDecorator('estimatedTimes', {
                    rules: [{ required: true, message: 'please input Estimated Time!' }]
                  })(
                    <Input />
                  )
                }
              </Form.Item>
            </Col>
          </Row>

          <Form.Item label="Step Action">
            {
              getFieldDecorator('stepActions', {
                rules: [{ required: true, message: 'please input Step Action!' }]
              })(
                <Input.TextArea rows={4} />
              )
            }
          </Form.Item>

          <Form.Item label="Step Details">
            {
              getFieldDecorator('stepDetails', {
                rules: [{ required: true, message: 'please input Step Details!' }]
              })(
                <Input.TextArea rows={4} />
              )
            }
          </Form.Item>

          <Row>
            <Col span={20}>
              <Form.Item label="Lesson PPT">
                {
                  getFieldDecorator('displayPage', {
                    rules: [{ required: true, message: 'please input Lesson PPT!' }],
                    initialValue: "TODO: lesson.ppt"
                  })(
                    <Input disabled />
                  )
                }
              </Form.Item>
            </Col>
            <Col span={4}>
              <Form.Item label="Page">
                {
                  getFieldDecorator('page', {
                    rules: [{ required: true, message: 'please input Lesson PPT page!' }]
                  })(
                    <Input />
                  )
                }
              </Form.Item>
            </Col>
          </Row>

          <Form.Item label="Table Resource">
            {
              getFieldDecorator('tableResource', {
                rules: [{ required: true, message: 'please select Table Resource!' }]
              })(
                <Select>
                  <Select.Option value="Table Resource1">Table Resource1</Select.Option>
                  <Select.Option value="Table Resource2">Table Resource2</Select.Option>
                  <Select.Option value="Table Resource3">Table Resource3</Select.Option>
                </Select>
              )
            }
          </Form.Item>

        </Form>

      </Modal >
    );
  }

}

AddTaskStepFormModal = Form.create()(AddTaskStepFormModal);

// task step table
const taskStepColums = [
  {
    title: 'Id',
    dataIndex: 'stepID',
    key: 'stepID',
  },
  {
    title: 'Action',
    dataIndex: 'stepActions',
    key: 'stepActions',
  },
  {
    title: 'Details',
    dataIndex: 'stepDetails',
    key: 'stepDetails',
  }
];

class AddTaskFormContent extends React.Component {

  state = {
    steps: [],
    showAddTaskStepFormModal: false
  }

  componentWillMount() {
    const { steps } = this.props;
    if (steps) {
      this.setState({ steps });
    }
  }

  handleAddTaskSave = () => {
    const form = this.props.form;
    form.validateFields((err, values) => {
      if (err) {
        message.warn('save task failed!');
      } else {
        const { onAddTaskSave } = this.props;
        onAddTaskSave(values);
        this.resetData();
      }
    });
  }

  handleAddTaskCancel = () => {
    const { onAddTaskCancel } = this.props;
    onAddTaskCancel();
    this.resetData();
  }

  handleAddStepButtonClicked = () => {
    this.setState({ showAddTaskStepFormModal: true });
  }

  handleAddTaskStepFormModalSave = (stepInfo) => {
    const { steps } = this.state;

    for (let i = 0; i < steps.length; ++i) {
      if (steps[i].stepID === stepInfo.stepID) {
        message.warn('same step ID');
        return;
      }
    }

    const updatedSteps = steps.concat(stepInfo);
    this.setState({ steps: updatedSteps, showAddTaskStepFormModal: false });
  }

  handleAddTaskStepFormModalCancel = () => {
    this.setState({ showAddTaskStepFormModal: false });
  }

  resetData = () => {
    const form = this.props.form;
    form.resetFields();
    this.setState({ steps: [] });
  }

  render() {
    const { visible, confirmLoading } = this.props;
    const { steps, showAddTaskStepFormModal } = this.state;

    const { getFieldDecorator } = this.props.form;
    return (
      <Modal
        visible={visible}
        confirmLoading={confirmLoading}
        onCancel={this.handleAddTaskCancel}
        onOk={this.handleAddTaskSave}
      >
        <Form >
          <Form.Item label="Task ID">
            {
              getFieldDecorator('taskID', {
                rules: [{ required: true, message: 'please input Task ID!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="Task Title">
            {
              getFieldDecorator('taskTitle', {
                rules: [{ required: true, message: 'please input Task title!' }]
              })(
                <Input />
              )
            }
          </Form.Item>

          {/* hide form ele, for save steps value */}
          <Form.Item label="steps" style={{ display: 'None' }}>
            {
              getFieldDecorator('steps', {
                initialValue: steps
              })(
                <Input />
              )
            }
          </Form.Item>

          <Row>
            <Col offset={23}>
              <div
                className={styles.addButton}
                onClick={this.handleAddStepButtonClicked}>
                <Icon type="plus" />
              </div>

            </Col>
          </Row>

          <Table
            rowKey={record => record.stepID}
            columns={taskStepColums}
            dataSource={steps} />

          <AddTaskStepFormModal
            visible={showAddTaskStepFormModal}
            onSaveStep={this.handleAddTaskStepFormModalSave}
            onCancel={this.handleAddTaskStepFormModalCancel} />
        </Form>
      </Modal>
    );
  }
}

const AddTaskFormModal = Form.create()(AddTaskFormContent);



class AddTaskInfoForm extends React.Component {
  handleAddTaskInfoSave = () => {
    const { id, onSaveTask } = this.props;
    if (!id) {
      message.warn('please save lesson info firstly!');
      return;
    }

    const form = this.form;
    form.validateFields((err, values) => {
      if (err) {
        this.setState({ confirmLoading: false });
        return;
      }

      onSaveTask(values);
    });
  };

  handleAddTaskInfoCancel = () => {
    const { onCancel } = this.props;
    onCancel();
  }

  saveFormRef = (form) => {
    this.form = form;
  };

  render() {
    const { visible } = this.props;

    return (
      <AddTaskFormModal
        ref={this.saveFormRef}
        visible={visible}
        onAddTaskCancel={this.handleAddTaskInfoCancel}
        onAddTaskSave={this.handleAddTaskInfoSave} />
    );
  }
}

export default AddTaskInfoForm;