import React from 'react';
import { Tabs } from 'antd';
import queryString from 'query-string';
import LessonHeader from './LessonHeader';
import LessonBaseInfoForm from '../forms/LessonBaseInfoForm';
import Task from './Task';
import LessonMaterial from './LessonMaterial';

import styles from '../../style/EditLesson.module.css';

const { TabPane } = Tabs;

class EditLesson extends React.Component {
  state = {
    activeKey: '1',
    programId: 0,//belongs to which program
    unitId: 0,//belongs to which unit
    programCode: '', //
    id: 0
  }

  componentWillMount() {
    const parsed = queryString.parse(this.props.location.search);
    const { id, unitId, programId } = parsed;
    this.setState({ id, unitId, programId });
  }


  handleTabChange = (activeKey) => {
    this.setState({ activeKey });
  }

  handleAddBaseInfo = (id) => {
    this.setState({ activeKey: '2', id });
  }

  render() {
    const { location } = this.props;
    const { activeKey, programCode, id } = this.state;
    return (
      <div>
        <LessonHeader location={location} />
        <Tabs
          className={styles.content}
          activeKey={activeKey}
          onChange={this.handleTabChange}
          type="card"
          tabBarGutter={4}>
          <TabPane
            tab="Lesson Info"
            key="1"
            className={styles.tabPane}>
            <LessonBaseInfoForm
              isEditType={true}
              lessonId={id}
              programCode={programCode}
              onAddBaseInfo={this.handleAddBaseInfo}
            />
          </TabPane>
          <TabPane
            tab="Tasks"
            key="2"
            className={styles.tabPane}>
            {activeKey === '2' ? <Task lessonID={id} /> : null}
          </TabPane>
          <TabPane
            tab="Material"
            key="3"
            className={styles.tabPane}>
            <LessonMaterial lessonID={id} />
          </TabPane>
          <TabPane
            tab="Permisson"
            key="4"
            className={styles.tabPane}>
            Content of Tab Pane 4
          </TabPane>
        </Tabs>,
    </div>);
  }
}

export default EditLesson;