import React from 'react';
import { Modal, Form, Icon, Input, Row, message } from 'antd';
import { addProgramUnit } from '../../axios/index';

import styles from '../../style/AddCampaignUnitForm.module.css'

const AddCampaignUnitFormModal = Form.create()(
  (props) => {
    const { visible, confirmLoading, onCancel, onOk } = props;
    const { getFieldDecorator } = props.form;
    return (
      <Modal
        visible={visible}
        confirmLoading={confirmLoading}
        onCancel={onCancel}
        onOk={onOk}
      >
        <Form >
          <Form.Item label="Unit ID">
            {
              getFieldDecorator('unitId', {
                rules: [{ required: true, message: 'please input Unit ID!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="Unit Name">
            {
              getFieldDecorator('unitName', {
                rules: [{ required: true, message: 'please input Unit name!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="Shared Problem with sloving">
            {
              getFieldDecorator('sharedProblem', {
                rules: [{ required: true, message: 'please input Shared Problem with sloving!' }]
              })(
                <Input.TextArea rows={4} />
              )
            }
          </Form.Item>
          <Row>
            <Form.Item label="Building the entirepreneurial Understanding">
              {
                getFieldDecorator('threeObjUnderstanding', {
                  rules: [{ required: true, message: 'please input Building the entirepreneurial Understanding!' }]
                })(
                  <Input.TextArea rows={4} />
                )
              }
            </Form.Item>
            <Form.Item label="Develop Language Capablity">
              {
                getFieldDecorator('threeObjLanguageCapablity', {
                  rules: [{ required: true, message: 'please input Develop Language Capablity!' }]
                })(
                  <Input.TextArea rows={4} />
                )
              }
            </Form.Item>
            <Form.Item label="Develop socio-emotional skills">
              {getFieldDecorator('threeObjSocioSkill', {
                rules: [{ required: true, message: 'please input socio-emotional skills' }],
              })(
                <Input.TextArea rows={4} />
              )}
            </Form.Item>
          </Row>
        </Form>
      </Modal>
    );
  }
);


class AddCampaignUnitForm extends React.Component {

  state = {
    visible: false,
    confirmLoading: false
  }

  showModal = () => {
    this.setState({
      visible: true
    });
  }

  handleOk = () => {
    const { id, onAddUnit } = this.props;
    if (!id) {
      message.warn('please save program base info firstly!');
      return;
    }

    this.setState({
      confirmLoading: true,
    });

    const form = this.form;
    form.validateFields((err, values) => {
      if (err) {
        this.setState({ confirmLoading: false });
        return;
      }

      const {
        unitId,
        unitName,
        sharedProblem,
        threeObjUnderstanding,
        threeObjLanguageCapablity,
        threeObjSocioSkill
      } = values;

      const threeObj = {
        understanding: threeObjUnderstanding,
        languageCapablity: threeObjLanguageCapablity,
        socioSkill: threeObjSocioSkill
      };

      const requestBody = {
        unitId,
        unitName,
        sharedProblem,
        threeObj: JSON.stringify(threeObj)
      };

      addProgramUnit(+id, requestBody).then(() => {
        this.setState({
          visible: false,
          confirmLoading: false,
        });
        onAddUnit();
      });
    });


  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  saveFormRef = (form) => {
    this.form = form;
  };

  render() {
    const { visible, confirmLoading } = this.state;

    return (
      <div>
        <div className={styles.addButton} onClick={this.showModal}>
          <Icon type="plus-circle" style={{ fontSize: '36px', color: '#08c' }} />
        </div>
        <AddCampaignUnitFormModal
          ref={this.saveFormRef}
          visible={visible}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
          onOk={this.handleOk} />
      </div>

    );
  }
}

export default AddCampaignUnitForm;