import React from 'react';
import { Table } from 'antd';
import { withRouter } from 'react-router-dom';
import UnitHeader from './UnitHeader';
import { listUnit } from '../../axios/index';

const columns = [
  {
    title: 'Unit ID',
    dataIndex: 'unitID',
    key: 'unitID',
    render: (text, record) =>
      <a href={`#/app/unit/edit/?id=${record.id}&programID=${record.programID}`}>
        {`${record.program.programID}/${text}`}
      </a>
  },
  {
    title: 'Program Name',
    key: 'programName',
    render: (text, record) => record.program && record.program.name
  },
  {
    title: 'Unit Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'School',
    dataIndex: 'school',
    key: 'school',
  }
];


const DEFAULT_PAGE_SIZE = 10;

class UnitHome extends React.Component {
  state = {
    unitList: [],
    count: 0
  }

  handleAddUnit = (event) => {
    event.preventDefault();
    this.props.history.push('/app/unit/add');
  }

  componentWillMount() {
    listUnit(0, DEFAULT_PAGE_SIZE).then(data => {
      if (data) {
        const { unitList, count } = data;
        this.setState({ unitList, count });
      }
    });
  }

  handlePageChange = (page, pageSize) => {
    const offset = (+page - 1) * pageSize;
    listUnit(offset, pageSize).then(data => {
      if (data) {
        const { unitList, count } = data;
        this.setState({ unitList, count });
      }
    });
  }

  render() {
    const { location } = this.props;
    const { unitList, count } = this.state;
    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }
    return (
      <div>
        <UnitHeader
          location={location}
          onAddClicked={this.handleAddUnit}
          showAddButton={true} />

        {unitList &&
          <Table
            columns={columns}
            rowKey={record => record.id}
            dataSource={unitList}
            pagination={pageOptions} />
        }

      </div>
    );
  }
}

export default withRouter(UnitHome);