import React from 'react';
import { Table } from 'antd';
import { withRouter } from 'react-router-dom';
import { listLesson } from '../../axios/index';
import LessonHeader from './LessonHeader';

const columns = [
  {
    title: 'LessonID',
    dataIndex: 'lessonNo',
    key: 'lessonNo',
    render: (text, record) => {
      const unit = record.unit;
      let programCode = '', unitCode = '';
      let programId = '';
      if (unit) {
        unitCode = unit.label ? unit.label : '';
        const program = unit.program;
        if (program) {
          programCode = program.programID ? program.programID : '';
          programId = program.id;
        }
      }
      return <a href={`#/app/lesson/edit/?id=${record.id}&unitId=${record.unitID}&programId=${programId}`}>
        {`${programCode}/${unitCode}/${text}`}
      </a>
    }

  },
  {
    title: 'Title',
    dataIndex: 'title',
    key: 'title',
  },
  {
    title: 'Focus',
    dataIndex: 'focus',
    key: 'focus'
  },
  {
    title: 'Imperatives',
    dataIndex: 'imperatives',
    key: 'imperatives',
  },
  {
    title: 'Description',
    dataIndex: 'description',
    key: 'description'
  }
];

const DEFAULT_PAGE_SIZE = 10;

class CampaignHome extends React.Component {

  state = {
    lessonList: [],
    count: 0
  }

  handleAddProgram = (event) => {
    event.preventDefault();
    this.props.history.push('/app/lesson/add');
  }

  handlePageChange = (page, pageSize) => {
    const offset = (+page - 1) * pageSize;
    listLesson(offset, pageSize).then(lessonList => {
      this.setState({ lessonList, count: lessonList.length });
    });
  }

  componentWillMount() {
    listLesson(0, DEFAULT_PAGE_SIZE).then(data => {
      if (!data) return;
      const { lessonList, count } = data;
      this.setState({ lessonList, count });
    });
  }


  render() {
    const { location } = this.props;
    const { lessonList, count } = this.state;

    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }

    return (
      <div>
        <LessonHeader
          location={location}
          onAddClicked={this.handleAddProgram}
          showAddButton={true} />

        {lessonList &&
          <Table
            columns={columns}
            rowKey={record => record.id}
            dataSource={lessonList}
            pagination={pageOptions} />
        }

      </div>
    );
  }
}

export default withRouter(CampaignHome);