import React from 'react';
import { Table, message } from 'antd';
import AddCampaignUnitForm from '../forms/AddCampaignUnitForm';
import { getUnitListByProgramId } from '../../axios/index';

import styles from '../../style/CampaignUnit.module.css'

const columns = [
  {
    title: 'Label',
    dataIndex: 'label',
    key: 'label',
    render: text => text
  },
  {
    title: 'Unit Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Shared Problem With Solving',
    dataIndex: 'sharedProblem',
    key: 'sharedProblem',
  },
  {
    title: 'Three Fold Learning Objectives',
    dataIndex: 'objectives',
    key: 'objectives',
    render: (value) => {
      let Understanding, LanguageCapablity, SocioSkill = null;
      if (value) {
        const parsedVal = JSON.parse(value);
        Understanding = parsedVal.understanding && <li>{parsedVal.understanding}</li>
        LanguageCapablity = parsedVal.languageCapablity && <li>{parsedVal.languageCapablity}</li>;
        SocioSkill = parsedVal.socioSkill && <li>{parsedVal.socioSkill}</li>;
      }
      return (
        <div>
          <p>Building the entirepreneurial Understanding</p>
          <ul>
            {Understanding}
            {LanguageCapablity}
            {SocioSkill}
          </ul>
        </div>
      )
    }
  }
];


const DEFAULT_PAGE_SIZE = 3;

export default class CampaignUnit extends React.Component {
  state = {
    isShowAddCampainUnitForm: false,
    unitList: [],
    count: 0
  }

  componentWillMount() {
    const { id } = this.props;
    if (!id) {
      message.warn('Please save program base info firstly!');
      return;
    };

    getUnitListByProgramId(+id, 0, DEFAULT_PAGE_SIZE).then(data => {
      const { unitList, count } = data;
      this.setState({ unitList, count });
    });
  }

  handlePageChange = (page, pageSize) => {
    // fetch new data from server
    const { id } = this.props;
    if (!id) {
      message.warn('Please save program base info firstly!');
      return;
    };

    const offset = (+page - 1) * pageSize;
    getUnitListByProgramId(+id, offset, pageSize).then(data => {
      const { unitList, count } = data;
      this.setState({ unitList, count });
    });
  }

  handleAddUnit = () => {
    const { id } = this.props;
    if (!id) return;

    getUnitListByProgramId(+id, 0, DEFAULT_PAGE_SIZE).then(data => {
      const { unitList, count } = data;
      this.setState({ unitList, count });
    });
  }


  render() {
    const { unitList, count } = this.state;
    const { id } = this.props;

    const pageOptions = {
      defaultPageSize: DEFAULT_PAGE_SIZE,
      onChange: this.handlePageChange,
      total: count
    }

    const ContentEle = (unitList && unitList.length !== 0) ?
      <Table
        className={styles.table}
        columns={columns}
        rowKey={record => record.id}
        dataSource={unitList}
        pagination={pageOptions} />
      : null;

    return (
      <div>
        {ContentEle}
        <AddCampaignUnitForm
          id={id}
          onAddUnit={this.handleAddUnit} />
      </div>
    );
  }
}