import React from 'react';
import { Modal, Button, Form, Input, Row, List, Card, Icon, Select, message } from 'antd';
import { getProcessByUnitId, upinsertLessonStructInfo, insertProcess } from '../../axios/index';
import AddProcessModal from '../unit/AddProcessModal';

import style from '../../style/LessonStructure.module.css';


class QuickAddLessonForm extends React.Component {

  handleOk = (id) => {
    const { onOk } = this.props;
    onOk(id);
  }

  handleCancel = () => {
    const { onCancel } = this.props;
    onCancel();
  }

  render() {
    const { visible, confirmLoading, id } = this.props;
    const { getFieldDecorator } = this.props.form;
    return (
      <Modal
        visible={visible}
        confirmLoading={confirmLoading}
        onCancel={this.handleCancel}
        onOk={() => this.handleOk(id)}>
        <Form>
          <Form.Item label="课程编号">
            {
              getFieldDecorator('lessonCode', {
                rules: [{ required: true, message: 'please input Lesson Code!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="课程标题">
            {
              getFieldDecorator('lessonTitle', {
                rules: [{ required: true, message: 'please input Lesson Title!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="课程设计时长">
            {
              getFieldDecorator('lessonDur', {
                rules: [{ required: true, message: 'please input Lesson Duration!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="课程Focus">
            {
              getFieldDecorator('lessonFocus', {
                rules: [{ required: true, message: 'please input Lesson Focus!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="Imperatives">
            {
              getFieldDecorator('lessonImper', {
                rules: [{ required: true, message: 'please input Lesson Imperatives!' }]
              })(
                <Input />
              )
            }
          </Form.Item>
          <Form.Item label="所属单元">
            {
              getFieldDecorator('parentUnit', {
                rules: [{ required: true, message: 'please Select parent unit!' }]
              })(
                <Select>
                  <Select.Option value="unit 1">unit 1</Select.Option>
                  <Select.Option value="unit 2">unit 2</Select.Option>
                  <Select.Option value="unit 3">unit 3</Select.Option>
                </Select>
              )
            }
          </Form.Item>
          <Form.Item label="课程编制人员">
            {
              getFieldDecorator('lessonTeacher', {
                rules: [{ required: true, message: 'please Select lesson teacher!' }]
              })(
                <Select>
                  <Select.Option value="teacher 1">teacher 1</Select.Option>
                  <Select.Option value="teacher 2">teacher 2</Select.Option>
                  <Select.Option value="teacher 3">teacher 3</Select.Option>
                </Select>
              )
            }
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}


const QuickAddLessonFormModal = Form.create()(QuickAddLessonForm);


class ProcessList extends React.Component {
  state = {
    showQuickAddLessonModal: false,
    processIdOfQuickAddLesson: 0
  }

  saveQuickAddLessonFormRef = (form) => {
    this.quickAddLessonForm = form;
  }

  onAddLessonClicked = (id) => {
    this.setState({
      showQuickAddLessonModal: true,
      processIdOfQuickAddLesson: id
    });
  }

  hanleQuickAddLessonFormOk = (id) => {
    const quickAddLessonForm = this.quickAddLessonForm;
    quickAddLessonForm.validateFields((err, values) => {
      if (!err) {
        //更新对应process的LessonList
        const { processList, onUpdateProcessList } = this.props;

        const updatedProcessList = processList.map(item => {
          if (item.id === id) {
            item.lessonList = item.lessonList.concat(values);
          }
          return item;
        });

        onUpdateProcessList(updatedProcessList);
        this.setState({ showQuickAddLessonModal: false });
      }
    });
  }

  hanleQuickAddLessonFormCancel = () => {
    this.setState({ showQuickAddLessonModal: false });
  }

  render() {
    const { showQuickAddLessonModal, processIdOfQuickAddLesson } = this.state;
    const { processList } = this.props;
    const { getFieldDecorator } = this.props.form;

    const FormItems = processList.map((item, index) => {
      const { id, processName, lessonList } = item;

      const lessonListContainsAdd = lessonList.concat('addButton');

      return (
        <Row key={`${processName}[${index}]`}>
          <Form.Item
            key={id}
            label="Process Name">
            {
              getFieldDecorator(`${processName}[processName]`, {
                rules: [{ required: true, message: 'please input process Name!' }],
                initialValue: `${processName}`
              })(<Input />)
            }
          </Form.Item>

          {/* 隐藏数据，保存的是每个process课程列表数据，方便表单双向绑定和获取数据 */}
          <Form.Item style={{ display: 'None' }}>
            {
              getFieldDecorator(`${processName}[lessonList]`, {
                initialValue: lessonList
              })(<Input />)
            }
          </Form.Item>

          <List
            grid={{ gutter: 16, column: 4 }}
            dataSource={lessonListContainsAdd}
            renderItem={item => {
              if (item === 'addButton') {
                return (
                  <List.Item onClick={() => this.onAddLessonClicked(id)}>
                    <Card
                      className={style.addLessonButton}
                      key={item}
                    >
                      <Icon type="plus" />
                    </Card>
                  </List.Item>
                );
              } else {
                return (
                  <List.Item>
                    <Card key={item}>Card content</Card>
                  </List.Item>
                );
              }

            }}
          />
        </Row>
      );
    });

    return (
      <div>
        <Form>
          {FormItems}
        </Form>
        <QuickAddLessonFormModal
          ref={this.saveQuickAddLessonFormRef}
          visible={showQuickAddLessonModal}
          id={processIdOfQuickAddLesson}
          onOk={this.hanleQuickAddLessonFormOk}
          onCancel={this.hanleQuickAddLessonFormCancel} />
      </div>

    );
  }
}

const ProcessListForm = Form.create()(ProcessList);



/**
 * 默认每个unit有8个process
 */
export default class LessonStructure extends React.Component {
  DEFAULT_PAGE_SIZE = 10;

  state = {
    processList: [],
    showAddProcessModal: false,
    confirmLoading: false
  }

  componentWillMount() {
    const { unitId } = this.props;
    if (unitId) {
      getProcessByUnitId(+unitId, 0, this.DEFAULT_PAGE_SIZE).then(data => {
        if (data) {
          const { processList } = data;
          const processListFromServer = processList.map(item => {
            return {
              id: item.id,
              processName: item.name,
              processObjective: item.objectives,
              lessonList: []
            };
          });
          this.setState({ processList: processListFromServer });
        }
      });
    }

  }

  handleAddProcessClick = () => {
    this.setState({ showAddProcessModal: true });
  }

  handleAddProcessCancel = () => {
    this.setState({ showAddProcessModal: false });
  }

  handleAddProcessOk = () => {
    const addProcessForm = this.addProcessForm;
    addProcessForm.validateFields((err, values) => {
      if (!err) {
        const { processList } = this.state;
        const { processName, processObjective } = values;

        const { unitId } = this.props;
        insertProcess(+unitId, processName, processObjective, '')
          .then((data) => {
            const { id } = data;
            const updatedProcessList = processList.concat({
              id,
              processName,
              processObjective,
              lessonList: []
            });

            this.setState({
              processList: updatedProcessList,
              showAddProcessModal: false,
              confirmLoading: false
            });
          });

      }
    });
  }

  saveAddProcessFormRef = (formmRef) => {
    this.addProcessForm = formmRef;
  }

  saveAddProcessListFormRef = (formmRef) => {
    this.addProcessListForm = formmRef;
  }

  handleSaveLessonStructure = (event) => {
    event.preventDefault();
    const addProcessListForm = this.addProcessListForm;
    addProcessListForm.validateFields((err, values) => {
      if (err) {
        console.log(err);
      } else {
        const { unitId } = this.props;
        if (!unitId) {
          console.error('unitId is null');
          message.warn('please save unit base info firstly.');
        } else {

          const processList = Object.keys(values).map(key => {
            return values[key];
          });
          upinsertLessonStructInfo(unitId, processList)
            .then(() => {
              message.success('save lesson structure success.');
            });
        }
      }
    });
  }

  handleUpdateProcessList = (updatedProcessList) => {
    this.setState({ processList: updatedProcessList })
  }

  render() {
    const { showAddProcessModal, confirmLoading, processList } = this.state;
    return (
      <div>
        <Button
          type="primary"
          onClick={this.handleAddProcessClick}>
          Add Process
        </Button>

        <ProcessListForm
          ref={this.saveAddProcessListFormRef}
          processList={processList}
          onUpdateProcessList={this.handleUpdateProcessList} />

        <Row className={style.action}>
          <Button
            onClick={this.handleSaveLessonStructure}
            type="primary">
            Save
          </Button>
          <Button
            className={style.btnCancel}
            type="default">
            Cancel
          </Button>
        </Row>


        <AddProcessModal
          ref={this.saveAddProcessFormRef}
          confirmLoading={confirmLoading}
          visible={showAddProcessModal}
          onCancel={this.handleAddProcessCancel}
          onOk={this.handleAddProcessOk} />
      </div >
    );
  }
}