import React from 'react';
import { Form, Row, Col, Input, Button } from 'antd';
import { getUnitInfoById, updateUnitBaseInfo, insertUnitBaseInfo } from '../../axios/index';
import styles from '../../style/UnitBaseInfoForm.module.css';

const { TextArea } = Input;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};


class UnitBaseInfoForm extends React.Component {

  state = {
    programType: [],
    country: [],
    schoolCode: [],
    programTypeValue: '',
    countryValue: '',
    schoolCodeValue: ''
  }

  componentWillMount() {
    const { isEditType, unitId } = this.props;
    if (isEditType && unitId) {
      getUnitInfoById(+unitId).then(data => {
        if (data) {
          const { objectives } = data;
          const threeObj = JSON.parse(objectives);
          const initData = { ...threeObj, ...data };

          this.props.form.setFieldsValue({
            programCode: initData.program && initData.program.programID,
            label: initData.label,
            name: initData.name,
            unitDurEstimate: initData.estimatedHours,
            unitDurActual: initData.actualHours,
            unitDesc: initData.description,
            unitProblemTarget: initData.targetedProblems,
            unitPossAuthProd: initData.possAuthProd,
            threeObjUnderstanding: initData.understanding,
            threeObjLanguageCapablity: initData.languageCapablity,
            threeObjSocioSkill: initData.socioSkill,
            unitOverview: initData.overviews
          });
        }
      });
    }
  }

  handleProgramTypeChange = value => {
    const { countryValue, schoolCodeValue } = this.state;
    this.props.form.setFieldsValue({
      unitCode: `${value}${countryValue}${schoolCodeValue}`
    });
    this.setState({ programTypeValue: value });
  }

  handleCountryChange = value => {
    const { programTypeValue, schoolCodeValue } = this.state;
    this.props.form.setFieldsValue({
      unitCode: `${programTypeValue}${value}${schoolCodeValue}`
    });
    this.setState({ countryValue: value })
  }

  handleSchoolCodeChange = value => {
    const { programTypeValue, countryValue } = this.state;
    this.props.form.setFieldsValue({
      unitCode: `${programTypeValue}${countryValue}${value}`
    });
    this.setState({ schoolCodeValue: value });
  }

  handleSubmit = (event) => {
    event.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (err) {
        console.log(err);
      } else {
        const {
          label,
          name,
          unitDurEstimate,
          unitDurActual,
          unitDesc,
          unitProblemTarget,
          unitOverview,
          threeObjUnderstanding,
          threeObjLanguageCapablity,
          threeObjSocioSkill } = values;

        const threeObj = {
          understanding: threeObjUnderstanding,
          languageCapablity: threeObjLanguageCapablity,
          socioSkill: threeObjSocioSkill
        };

        const { isEditType, programID, unitId } = this.props;

        let requestBody = {
          id: +unitId,
          programID: +programID,
          unitID: label,
          label,
          name,
          estimatedHours: unitDurEstimate,
          actualHours: unitDurActual,
          description: unitDesc,
          targetedProblems: unitProblemTarget,
          createdProducts: '',
          sharedProblem: '',
          objectives: JSON.stringify(threeObj),
          overviews: unitOverview,
          status: ''
        };

        if (isEditType) {
          updateUnitBaseInfo(requestBody).then((data) => {
            if (data) {
              const { onAddBaseInfo } = this.props;
              const { id } = data;
              onAddBaseInfo(id);
            }
          });
        } else {
          insertUnitBaseInfo(requestBody).then(data => {
            if (data) {
              const { onAddBaseInfo } = this.props;
              const { id } = data;
              onAddBaseInfo(id);
            }
          });
        }


      }
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;
    const { isEditType } = this.props;

    return (
      <Form onSubmit={this.handleSubmit}>
        <Row>
          <Col span={12}>
            <Form.Item label='Program Code' {...formItemLayout}>
              {getFieldDecorator('programCode', {
                rules: [{ required: true, message: 'please input Program Code' }]
              })(
                isEditType ? <Input disabled /> : <Input />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item label='Unit Code' {...formItemLayout}>
              {getFieldDecorator('label', {
                rules: [{ required: true, message: 'please input Unit Code' }]
              })(
                <Input style={{ width: 40, marginLeft: 8 }} />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col span={12}>
            <Form.Item label='Name' {...formItemLayout}>
              {
                getFieldDecorator('name', {
                  rules: [{ required: true, message: 'please input Unit Name' }]
                })(
                  < Input />
                )
              }
            </Form.Item>
          </Col>
          <Col span={12}>
            <Row>
              <Col span={5} className={styles.durLabel}>
                <span>Duduration: </span>
              </Col>
              <Col span={18}>
                <Form.Item label='Estimate' {...formItemLayout} >
                  {
                    getFieldDecorator('unitDurEstimate', {
                      rules: [{ required: true, message: 'please input Unit Duduration' }]
                    })(< Input style={{ maxWidth: 50 }} />)
                  }
                  <span className={styles.durUnit}> Hours</span>
                </Form.Item>
                <Form.Item label='Actual' {...formItemLayout} >
                  {
                    getFieldDecorator('unitDurActual', {
                      rules: [{ required: true, message: 'please input Unit Actual' }]
                    })(< Input style={{ maxWidth: 50 }} />)
                  }
                  <span className={styles.durUnit}> Hours</span>
                </Form.Item>
              </Col>
            </Row>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Item label="Description:">
              {getFieldDecorator('unitDesc', {
                rules: [{ required: true, message: 'please input unit description' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Item label="The problems targeted in this unit:">
              {getFieldDecorator('unitProblemTarget', {
                rules: [{ required: true, message: 'please input problems targeted' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Item label="Possible Authentic Products to be created in the unit">
              {getFieldDecorator('unitPossAuthProd', {
                rules: [{ required: true, message: 'please input Possible Authentic Products' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>
        </Row>


        <Row>
          <h3 style={{ marginLeft: 10 }}>Three Fold Learning Objective</h3>
          <Col>
            <Form.Item label="Building the entrepreneurial Understanding">
              {getFieldDecorator('threeObjUnderstanding', {
                rules: [{ required: true, message: 'please input entrepreneurial Understanding' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop Language Capablity">
              {getFieldDecorator('threeObjLanguageCapablity', {
                rules: [{ required: true, message: 'please input Develop Language Capablity' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop socio-emotional skills">
              {getFieldDecorator('threeObjSocioSkill', {
                rules: [{ required: true, message: 'please input socio-emotional skills' }]
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

        </Row>


        <Form.Item label="Unit overview">
          {getFieldDecorator('unitOverview', {
            rules: [{ required: true, message: 'please input unit overview' }]
          })(
            <TextArea />
          )}
        </Form.Item>

        <Row>
          <Col span={2} offset={10}>
            <Button type="primary" htmlType="submit">Save</Button>
          </Col>
          <Col span={2}>
            <Button >Cancel</Button>
          </Col>
        </Row>
      </Form>

    );
  }
}

export default Form.create()(UnitBaseInfoForm);