import React from 'react';
import { Form, Input, Row, Col, Select, Button } from 'antd';
import { getProgramIdInfo, getSemesterByCountry, getProgramInfoById } from '../../axios/index';
import styles from '../../style/CampaignBaseInfoForm.module.css'
import { upinsertProgramBaseInfo } from '../../axios/index';

const { TextArea } = Input;

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 12 },
};


class ProgramIdSelect extends React.Component {
  render() {
    const { programType, country, schoolCode } = this.props;
    const { onProgramTypeChange, onCountryChange, onSchoolCodeChange } = this.props;

    const ProgramTypeSelect =
      <Select
        placeholder="ProgramType"
        onChange={onProgramTypeChange}>
        {
          programType.map(item => <Select.Option key={item.code} value={item.code}>{item.value}</Select.Option>)
        }
      </Select>

    const CountrySelect =
      <Select
        placeholder="Country"
        onChange={onCountryChange}>
        {
          country.map(item =>
            <Select.Option key={item.code} value={item.code}>{item.value}</Select.Option>
          )
        }
      </Select>;

    const SchoolCodeSelect =
      <Select
        placeholder="SchoolCode"
        onChange={onSchoolCodeChange}
      >
        {
          schoolCode.map(item =>
            <Select.Option key={item.code} value={item.code}>{item.value}</Select.Option>
          )
        }
      </Select>;

    return (<Col span={12}>
      <Row gutter={{ sm: 6 }}>
        <Col span={6}>
          {ProgramTypeSelect}
        </Col>
        <Col span={6}>
          {CountrySelect}
        </Col>
        <Col span={6}>
          {SchoolCodeSelect}
        </Col>
      </Row>
    </Col>);
  }
}


const renderSemesterList = (semesterList) => {
  return semesterList.map((item, index) => {
    return <Select.Option key={item} value={item}>{item}</Select.Option>
  }
  );
}

/**
 * 如果是编辑模式，填充后台数据
 * 如果是新增模式，只拿一些下拉列表的数据
 */
class CampaignBaseInfoForm extends React.Component {

  state = {
    programType: [],
    country: [],
    schoolCode: [],
    grade: [],
    semesterList: [],
    programTypeValue: '',
    countryValue: '',
    schoolCodeValue: ''
  }

  componentWillMount() {
    getProgramIdInfo().then(data => {
      if (data) {
        const { programType, country, schoolCode, grade } = data;
        this.setState({ programType, country, schoolCode, grade });
      }
    });

    const { isEditType, id } = this.props;
    console.log(isEditType, id);
    if (isEditType && id) {
      getProgramInfoById(+id).then(data => {
        const form = this.props.form;
        const { objectives } = data;
        const threeObj = objectives ? JSON.parse(objectives) : {};

        const {
          languageCapablity,
          socioSkill,
          understanding } = threeObj;

        form.setFieldsValue({
          programId: data.programID,
          programName: data.name,
          programOwer: data.owner,
          programFeature: data.feature,
          grade: data.grade,
          semester: data.semester,
          ageLevel: data.agelevel,
          threeObjUnderstanding: understanding || '',
          threeObjLanguageCapablity: languageCapablity || '',
          threeObjSocioSkill: socioSkill || '',
          programOverview: data.overview
        });
      });
    }
  }

  handleSubmit = (event) => {
    event.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // send to backend
        const { programId,
          programName,
          grade,
          programOwner,
          semester,
          programFeature,
          ageLevel,
          threeObjUnderstanding,
          threeObjLanguageCapablity,
          threeObjSocioSkill,
          programOverview } = values;

        const threeObj = [
          { understanding: threeObjUnderstanding },
          { languageCapablity: threeObjLanguageCapablity },
          { socioSkill: threeObjSocioSkill }
        ];
        let requestBody = {
          programID: programId,
          programName,
          grade,
          programOwner,
          semester,
          programFeature,
          ageLevel,
          programOverview,
          threeObj: JSON.stringify(threeObj)
        };

        //id: program id in database
        upinsertProgramBaseInfo(requestBody).then((id) => {
          const { onAddBaseInfo } = this.props;
          onAddBaseInfo(id);
        });
      }
    });
  }


  handleProgramTypeChange = value => {
    const { countryValue, schoolCodeValue } = this.state;
    this.props.form.setFieldsValue({
      programId: `${value}${countryValue}${schoolCodeValue}`
    });
    this.setState({ programTypeValue: value });
  }

  handleCountryChange = value => {
    const { programTypeValue, schoolCodeValue } = this.state;
    this.props.form.setFieldsValue({
      programId: `${programTypeValue}${value}${schoolCodeValue}`
    });

    getSemesterByCountry(value).then((semesterList) => {
      console.log('handleCountryChange', semesterList);
      this.setState({ semesterList: semesterList, countryValue: value });
    });
  }

  handleSchoolCodeChange = value => {
    const { programTypeValue, countryValue } = this.state;
    this.props.form.setFieldsValue({
      programId: `${programTypeValue}${countryValue}${value}`
    });
    this.setState({ schoolCodeValue: value });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const { programType, country, schoolCode, grade, semesterList } = this.state;

    const GradeSelectItem = grade && grade.map(item =>
      <Select.Option key={item.code} value={item.code}>{item.value}</Select.Option>
    );

    const SemesterSelectItem = semesterList && renderSemesterList(semesterList);

    return (
      <Form onSubmit={this.handleSubmit}>
        <Row>
          <Col span={12}>
            <Form.Item label='ProgramCode' {...formItemLayout}>
              {/**数据库里面存的是program_id其实是programCode */}
              {getFieldDecorator('programId', {
                rules: [{ required: true, message: 'please input Program Code' }]
              })(
                <Input disabled={true} />
              )}
            </Form.Item>
          </Col>
          <ProgramIdSelect
            programType={programType}
            country={country}
            schoolCode={schoolCode}
            onProgramTypeChange={this.handleProgramTypeChange}
            onCountryChange={this.handleCountryChange}
            onSchoolCodeChange={this.handleSchoolCodeChange} />
        </Row>

        <Row>
          <Col span={12}>
            <Form.Item label="ProgramName" {...formItemLayout}>
              {getFieldDecorator('programName', {
                rules: [{ required: true, message: 'please input Program Name' }],
              })(
                <Input placeholder="Program Name" />
              )}
            </Form.Item>
            <Form.Item label="ProgramOwer" {...formItemLayout}>
              {getFieldDecorator('programOwer', {
                rules: [{ required: true, message: 'please input Program Ower' }],
              })(
                <Input placeholder="Program Ower" />
              )}
            </Form.Item>
            <Form.Item label="ProgramFeature" {...formItemLayout}>
              {getFieldDecorator('programFeature', {
                rules: [{ required: true, message: 'please input Program Feature' }],
              })(
                <Input placeholder="Program Feature" />
              )}
            </Form.Item>
          </Col>

          <Col span={12}>
            <Form.Item label="Grade" {...formItemLayout}>
              {getFieldDecorator('grade', {
                rules: [{ required: true, message: 'please select Program Grade' }],
              })(
                <Select>
                  {GradeSelectItem}
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Semester" {...formItemLayout}>
              {getFieldDecorator('semester', {
                rules: [{ required: true, message: 'please select Program Semester' }],
              })(
                <Select>
                  {SemesterSelectItem}
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Age Level" {...formItemLayout}>
              {getFieldDecorator('ageLevel', {
                rules: [{ required: true, message: 'please select Program Age Level' }],
              })(
                <Select>
                  <Select.Option value="Age Level 1">Age Level 1</Select.Option>
                  <Select.Option value="Age Level 2">Age Level 2</Select.Option>
                  <Select.Option value="Age Level 3">Age Level 3</Select.Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <h3 style={{ marginLeft: 10 }}>Three Fold Learning Objective</h3>
          <Col>
            <Form.Item label="Building the entrepreneurial Understanding">
              {getFieldDecorator('threeObjUnderstanding', {
                rules: [{ required: true, message: 'please select Program Age Level' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop Language Capablity">
              {getFieldDecorator('threeObjLanguageCapablity', {
                rules: [{ required: true, message: 'please input Develop Language Capablity' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

          <Col>
            <Form.Item label="Develop socio-emotional skills">
              {getFieldDecorator('threeObjSocioSkill', {
                rules: [{ required: true, message: 'please input socio-emotional skills' }],
              })(
                <TextArea />
              )}
            </Form.Item>
          </Col>

        </Row>

        <Row>
          <Form.Item label="Program overview">
            {getFieldDecorator('programOverview', {
              rules: [{ required: true, message: 'please input program overview' }],
            })(
              <TextArea />
            )}
          </Form.Item>
        </Row>

        <Row className={styles.action}>
          <Col span={2} offset={10}>
            <Button type="primary" htmlType="submit">Save</Button>
          </Col>
          <Col span={2}>
            <Button >Cancel</Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create()(CampaignBaseInfoForm);