import React from 'react';
import { Tabs } from 'antd';
import LessonHeader from './LessonHeader';
import LessonBaseInfoForm from '../forms/LessonBaseInfoForm';
import Task from './Task';
import LessonMaterial from './LessonMaterial';

import styles from '../../style/AddLesson.module.css';

const { TabPane } = Tabs;

export default class AddLesson extends React.Component {
  state = {
    activeKey: '1',
    programId: 0,//belongs to which program
    unitId: 0,//belongs to which unit
    programCode: '',
    lessonId: ''
  }

  handleTabChange = (activeKey) => {
    this.setState({ activeKey });
  }

  handleAddBaseInfo = (lessonId) => {
    this.setState({ activeKey: '2', lessonId });
  }

  render() {
    const { location } = this.props;
    const { activeKey, programCode, lessonId } = this.state;

    return (
      <div>
        <LessonHeader location={location} />
        <Tabs
          className={styles.content}
          activeKey={activeKey}
          onChange={this.handleTabChange}
          type="card"
          tabBarGutter={4}>
          <TabPane
            tab="Lesson Info"
            key="1"
            className={styles.tabPane}>
            <LessonBaseInfoForm
              programCode={programCode}
              onAddBaseInfo={this.handleAddBaseInfo}
            />
          </TabPane>
          <TabPane
            tab="Tasks"
            key="2"
            className={styles.tabPane}>
            {activeKey === '2' ? <Task lessonID={lessonId} /> : null}
          </TabPane>
          <TabPane
            tab="Material"
            key="3"
            className={styles.tabPane}>
            <LessonMaterial lessonID={lessonId} />
          </TabPane>
          <TabPane
            tab="Permisson"
            key="4"
            className={styles.tabPane}>
            Content of Tab Pane 4
          </TabPane>
        </Tabs>,
    </div>);
  }
}