import React from 'react';
import { Tabs } from 'antd';
import queryString from 'query-string';
import UnitHeader from './UnitHeader';
import UnitBaseInfoForm from '../forms/UnitBaseInfoForm';
import LessonStructure from './LessonStructure';
import MaterialForm from '../forms/MaterialForm';

import styles from '../../style/EditUnit.module.css';

const { TabPane } = Tabs;

class EditHome extends React.Component {
  state = {
    activeKey: '1',
    id: 0,
    programID: 0
  }

  componentWillMount() {
    const parsed = queryString.parse(this.props.location.search);
    const { id, programID } = parsed;
    this.setState({ id, programID });
  }

  handleTabChange = (activeKey) => {
    this.setState({ activeKey });
  }

  handleAddBaseInfo = (id) => {
    this.setState({ activeKey: '2', id });
  }

  //TODO: SAVE
  handleMaterialSave = (values) => {

  }

  render() {
    const { location } = this.props;
    const { activeKey, id, programID } = this.state;

    return (
      <div>
        <UnitHeader location={location} />
        <Tabs
          className={styles.content}
          activeKey={activeKey}
          onChange={this.handleTabChange}
          type="card"
          tabBarGutter={4}>
          <TabPane tab="单元基础信息" key="1" className={styles.tabPane}>
            <UnitBaseInfoForm
              programID={programID}
              unitId={id}
              isEditType={true}
              onAddBaseInfo={this.handleAddBaseInfo}
            />
          </TabPane>
          <TabPane tab="课程结构" key="2" className={styles.tabPane}>
            {activeKey === '2' ? <LessonStructure unitId={id} /> : null /*force update*/}
          </TabPane>
          <TabPane tab="Material" key="3" className={styles.tabPane}>
            {activeKey === '3' ? <MaterialForm unitId={id} onSave={this.handleMaterialSave} /> : null /*force update*/}
          </TabPane>
          <TabPane tab="角色权限" key="4" className={styles.tabPane}>
            Content of Tab 角色权限
          </TabPane>
          <TabPane tab="资料管理" key="5" className={styles.tabPane}>
            Content of Tab 资料管理
          </TabPane>
        </Tabs>,
    </div>);
  }
}

export default EditHome;