import axios from 'axios';
import { message } from 'antd';

/**
 * 公用get请求
 * @param url       接口地址
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
 */
export const get = ({ url, msg = '接口异常', config }) =>
  axios
    .get(url, config)
    .then(res => res.data)
    .catch(err => {
      console.log(err);
      message.warn(msg);
    });

/**
 * 公用post请求
 * @param url       接口地址
 * @param data      接口参数
 * @param msg       接口异常提示
 * @param headers   接口所需header配置
 */
export const post = ({ url, data, msg = '接口异常', config }) =>
  axios
    .post(url, data, config)
    .then(res => res.data)
    .then(data => {
      if (data.isSuccess) {
        return data.data;
      } else {
        message.warn(msg);
        return {};
      }
    })
    .catch(err => {
      console.log(err);
      message.warn(msg);
    });
